@import url(https://fonts.googleapis.com/css?family=Muli:400,700);
.backdrop {
  background-size: 150%;
  background-position: 50%;
  /* Add the blur effect */
  filter: blur(0.75);
  -webkit-filter: blur(0.75rem);
  position: absolute;
  top: -2rem;
  left: -2rem;
  bottom: -2rem;
  right: -2rem;
  z-index: -1;
}

.album-info {
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 800px;
}

.album-info--cover img {
  width: 19rem;
}

.album-info--meta {
  padding-left: 2rem;
  color: #fff;
}

.album-info--meta .heading {
  font-size: 2rem;
  margin: 0 0 1rem 0;
}

.external-action {
  display: inline-block;
  padding: 1rem;
  background: #191414;
  border-radius: 0.1875rem;
  color: white;
  font-size: 1.125rem;
  text-decoration: none;
  -webkit-transition: all 0.1s ease;
  transition: all 0.1s ease;
  margin-right: 1rem;
}

.external-action:hover {
  color: #191414;
  background: white;
}

.external-action svg {
  margin-right: 1rem;
  display: inline-block;
  vertical-align: middle;
}

.loading-cover {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #191414;
}

.progress {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 0.5rem;
}

.progress--bar {
  height: 0.5rem;
  background: yellow;
  -webkit-transition: width 0.2s ease;
  transition: width 0.2s ease;
}

body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-family: "Muli", sans-serif;
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  background: rgba(0, 0, 0, 0.6);
  overflow: hidden;
  position: relative;
}

